export default {
  data () {
    return {
      couponList: []  // 优惠券列表
    }
  },
  mounted () {
    let that = this
    that.$nextTick(() => {
      that.getCouponList()
      that.setWechat('http://h5.06baobao.com/#/')
    })
  },
  methods: {
    // 获取优惠券列表
    getCouponList () {
      let that = this
      let data = {
        token: localStorage.getItem('token'),
        type: 1
      }
      that.axios({
        method: 'POST',
        url: 'interface/Personal_center/my_coupon',
        data: data
      }).then((res) => {
        console.log('优惠券', res)
        if (res.code !== 1) {
          that.showAlert(res.msg)
          return false
        }
        that.couponList = res.data.coupon
      })
    }
  }
}



